#root {
  height: 100%;
}

html {
  background-color: #6eb1ff;
}

body {
  position: relative;
  margin: 0;
  height: 100vh;
  -webkit-overflow-scrolling: touch;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  user-select: none;
  overflow-y: visible;
  overflow-x: hidden;
  image-rendering: pixelated;
  font-family: "Chinese", "Twemoji", "minecraft-chat";
  font-size: 1.5dvh;
}

p {
  all: unset;
}

@font-face {
  font-family: minecraft;
  src: url("/src/assets/fonts/MinecraftRegular.otf");
  /* src: url("fonts/F77-Minecraft.ttf"); */
}

@font-face {
  font-family: minecraft-chat;
  /* src: url("fonts/MinecraftRegular.otf"); */
  src: url("/src/assets/fonts/F77-Minecraft.ttf");
}

@font-face {
  font-family: "Twemoji 2";
  src: url("https://cdn.jsdelivr.net/npm/twemoji-colr-font@latest/twemoji.woff2") format("woff2");
}

