.stats {
  display: flex;
  color: white;
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.6)),
    url(/src/assets/images/blocks/stone.png);
  background-size: 5vh;
  background-clip: padding-box;
  border: black 0.2vh solid;
  border-radius: 0.4vh;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: max-content;
  width: min(85vw, 50vh);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1vh;
  padding: 2vh;
  text-shadow: 0.2vh 0.2vh black;
}

.closeStats {
  position: absolute;
  top: 1vh;
  right: 1vh;
  transform-origin: top left;
  width: 3vh;
  height: 3vh;
  font-size: 3vh;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.title {
  font-size: 3vh;
}

.subtitle {
  font-size: 2vh;
}